body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

html{height:100%;text-align:center;background:-webkit-gradient(linear, left top, right top, from(#3b2fff), to(#68e2bb));background:linear-gradient(90deg, #3b2fff 0%, #68e2bb 100%);background-repeat:no-repeat;background-attachment:fixed}.grid-container{display:grid;grid-row-gap:2em;row-gap:2em;min-height:100vh;grid-template-columns:100%;grid-template-rows:5em auto auto 5em;grid-template-areas:"nav" "card" "quote" "footer"}.navbar{grid-area:nav;background:#fff;display:flex;justify-content:flex-start;align-items:center}.card-container{display:flex;justify-content:center;align-items:center;grid-area:card;margin:0 2em}.quote{display:flex;justify-content:center;align-items:center;grid-area:quote;color:#fff;margin:5em}.footer-container{display:flex;justify-content:center;align-items:center;grid-area:footer;background:#fff}@media only screen and (min-width: 1000px){.grid-container{margin:0;grid-template-columns:1fr 1fr 1fr;grid-template-rows:5em auto 5em;grid-template-areas:"nav nav nav"  "card quote quote" "footer footer footer"}}

.card{min-height:0;min-width:200px}.card-container{display:grid;height:auto;background-color:#fff;grid-template-columns:repeat(4, 1fr);grid-template-rows:1fr 3fr 1fr 1fr;grid-template-areas:"card-thumbnail card-h1 card-h1 card-h1" "card-image card-image card-image card-image" "card-blurb card-blurb card-blurb card-blurb" "card-action-1 card-action-2 heart-icon share-icon"}.card-thumbnail{grid-area:card-thumbnail;max-height:75px;min-width:50%;margin:0 1em;justify-self:center;overflow:hidden;border-radius:50%}.card-thumbnail>img{width:100%}h1{grid-area:card-h1;margin:0 0.5em;font-size:1.5em;text-align:left}h2{display:none;grid-area:card-h2;margin-left:0.5em;margin-top:0;text-align:left}.card-image{grid-area:card-image}.card-image>img{min-width:100%;max-width:100%;height:-webkit-max-content;height:-moz-max-content;height:max-content;object-fit:cover}.card-description{grid-area:card-blurb;padding:0 2em}.action{color:#3b2fff;cursor:pointer;font-weight:bold;justify-self:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.action.active{display:none}.icon-container{width:m}.icon-container>.icon :hover{cursor:pointer;color:#68e2bb}.icon-container.heart{grid-area:heart-icon;text-align:right}.icon-container.heart>.active{color:red}.icon-container.share{grid-area:share-icon}@media only screen and (min-width: 1000px){.card-container{display:grid;height:100%;background-color:white;grid-template-columns:repeat(4, 1fr);grid-template-rows:0.5fr 0.5fr 3fr 1fr 1fr;grid-template-areas:"card-thumbnail card-h1 card-h1 card-h1" "card-thumbnail card-h2 card-h2 card-h2" "card-image card-image card-image card-image" "card-blurb card-blurb card-blurb card-blurb" "action action heart-icon share-icon"}h2{display:block}h1{margin-bottom:0;margin-left:0.5em;font-size:2em}}

.nav-logo{height:50%;padding-left:3em}

